        #compassBlock{
            height:30%;
            width:30%;
            position:absolute;
            top: 35%;
            left:33%;
            display:flex;
            justify-content:center;
            align-items:center;
            animation:goToInventory 3s both;
        }   

        @keyframes goToInventory {
            30% {top:35%;left:33%;height:30%;width:30%;}
            90% {top:15%;left:22%;height:7%;width:7%;opacity:1}
            100% {top:15%;left:22%;height:7%;width:7%;opacity:0}
        }

        .compassToInventoryContainer{
            position:relative;
            height:100%;width:100%;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            padding-bottom: 10%;
        }

        #choiceTitleBlock{
            z-index:1;
            height: 100%;
            width:100%;
            display:flex;
            justify-content:center;
            align-items:center;
            animation:goUp 1s 1s both;
        }

        @keyframes goUp {
            0% {height:100%}
            100% {height:15%}
        }

            #choiceTitleTxt{
                margin:0;
                color:white;
                font-size:6vw;
                font-family: 'Sansita Swashed',cursive;
                font-weight: 700;
            }
            
        #choiceBlock{
            z-index:1;
            width:100%;
            height:0%;
            display:flex;
            justify-content:center;
            align-items:center;
            animation : takePlace 2s 1s both;
        }

        @keyframes takePlace{
            0% {height:0%;opacity:0}
            50%{height:85%;opacity:0}
            100% {height:85%;opacity:1}
        }

