.objects{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content:center;
    align-items:center;
}

    .object{
        /* position:relative; */
    }

    .objectImg{
        width:100px;
        height:auto;
        filter: drop-shadow(5px 5px 7px rgba(255, 233, 37, 0.5)) drop-shadow(-5px -5px 7px rgba(255, 233, 37, 0.5)) drop-shadow(5px -5px 7px rgba(255, 233, 37, 0.5)) drop-shadow(-5px 5px 7px rgba(255, 233, 37, 0.5));
    }

    #goToInventoryObj{
        position:absolute;
        top:0%;
        left:0%;
        animation: fadeIn 1s both;
    }

    @keyframes goToInventoryObj {
        /* 70% {top:-17.9%;left:-23.1%;opacity:1} */
        60% {opacity:1}
        100% {top:-55%;left:-33%;opacity:0}
    }
